<template>
	<div>
		<ul class="farminglist" v-if="farminglist.length">
			<li class="farmingitem" v-for="(farming, i) in farminglist" :key="i">
				<div class="line"></div>
				<div class="farm-info">
					<p class="title">
						<span class="name">{{ farming.sysMissionCategoryName }}</span>
						<span class="time" v-if="farming.completeTime">{{ farming.completeTime | znDateFormat('yyyy.MM.dd') }}</span>
						<span class="person" v-if="farming.chargeUserName"><img src="../../assets/images/gismap/icon-1.png" alt="" />{{ farming.chargeUserName }}</span>
					</p>
					<p class="task-info">{{ farming.missionDesc }}</p>
					<p class="info"></p>
					<div class="source-list" v-if="farming.coMissionInputVOList.length">
						<div class="source">
							<p class="description" v-for="(source, index) in farming.coMissionInputVOList" :key="index">
								<span class="name">{{ source.agriResName }}</span>
								<span class="specification" v-if="source.dosageType === 1 && source.averageDosageValue">{{ source.averageDosageValue }} {{ source.unitName }}/亩</span>
								<span class="specification" v-if="source.dosageType === 2 && source.totalDosageValue">{{ source.totalDosageValue }} {{ source.unitName }}</span>
								<span class="factory" v-if="source.productor">厂家：{{ source.productor }}</span>
							</p>
						</div>
					</div>
					<div class="farming-imgs" v-if="!!farming.feedbackImgUrls">
						<img
							v-for="(imgitem, i) in farming.feedbackImgUrls.split(',').slice(0, 5)"
							:src="imgitem | aliimg(58, 58, true)"
							alt=""
							:key="i"
							@click="showbigImgfn(farming.feedbackImgUrls, i)"
						/>
					</div>
				</div>
			</li>
		</ul>
		<div class="nodata" v-else-if="!farminglist.length">
			<img src="../../assets/images/no_inputs.png" alt="无数据" />
			<p>暂无农事记录</p>
		</div>
		<div class="loading" v-else v-loading="loader" element-loading-text="加载中..."></div>
		<views :showArrow="true" :pics="viewBigImgs" v-if="viewImgDialog" @viewerListen="viewImgDialog = false" :index="activeViewImgIndex"></views>
	</div>
</template>
<script>
import {znDateFormat} from '@/common/fn/index';
import Views from '@/components/common/viewPics';
export default {
	data() {
		return {
			loader: false,
			//查看大图
			viewImgDialog: false,
			viewBigImgs: [],
			activeViewImgIndex: 0,
		};
	},
	props: {
		farminglist: {
			type: Array,
			default: [],
		},
	},
	created() {},
	methods: {
		showbigImgfn(imgurlstr, active) {
			var imgarr = imgurlstr.split(',');
			if (imgarr.length === 0) return;
			this.viewBigImgs = imgarr;
			this.viewImgDialog = true;
			this.activeViewImgIndex = active;
		},
	},
	filters: {},
	components: {
		Views,
	},
};
</script>
<style scoped lang="scss">
@mixin flex($x: center, $y: center) {
	display: flex;
	display: -webkit-flex;
	flex-flow: row nowrap;
	justify-content: $x;
	align-items: $y;
}

@mixin fontS($s: 14px) {
	font-size: $s;
}

.farminglist {
	padding: 0 35px 0 40px;
	.farmingitem {
		@include flex(flex-start, flex-start);
		position: relative;
		.line {
			width: 2px;
			height: 100%;
			background: #19aeb3;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			&:after {
				content: '.';
				font-size: 0;
				display: inline-block;
				width: 10px;
				height: 10px;
				background-color: #19aeb3;
				border-radius: 50%;
				position: absolute;
				top: -4px;
				left: -4px;
			}
		}
		&:last-child .line {
			bottom: auto;
			height: 5px;
		}
		.farm-info {
			width: 605px;
			background: #061f36;
			margin-bottom: 12px;
			margin-left: 28px;
			padding: 10px;
			box-sizing: border-box;
			text-align: left;
			&:after {
				content: '.';
				font-size: 0;
				display: inline-block;
				width: 0;
				height: 0;
				border-top: 0 solid transparent;
				border-bottom: 16px solid transparent;
				border-right: 16px solid #061f36;
				position: absolute;
				top: 0px;
				left: 12px;
			}
			p.title,
			p.task-info {
				display: flex;
				align-items: center;
				margin: 10px 0;
				font-size: 16px;
				font-weight: bold;
				color: #a1eff8;
				text-align: left;
				span.name {
					display: inline-block;
					max-width: 440px;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
				}
				span.time {
					display: inline-block;
					padding: 0 4px;
					line-height: 18px;
					background: #06567c;
					border-radius: 4px;
					font-size: 14px;
					color: #6dc1cb;
					margin-left: 10px;
					vertical-align: 4px;
					white-space: nowrap;
				}
				span.person {
					display: inline-block;
					font-size: 12px;
					color: #6dc1cb;
					float: right;
					vertical-align: 4px;
					white-space: nowrap;
					flex: 1;
					text-align: right;
					padding-left: 6px;
					img {
						vertical-align: -5px;
						margin-right: 2px;
					}
				}
			}
			p.task-info {
				color: #6dc1cb;
				font-size: 14px;
				font-weight: normal;
				line-height: 20px;
				margin: 10px 0 4px 0;
			}
			.source-list {
				width: 100%;
				overflow: hidden;
				p.description {
					width: 100%;
					line-height: 30px;
					padding: 0 11px;
					box-sizing: border-box;
					background: #051423;
					border-radius: 4px;
					text-align: left;
					font-size: 12px;
					color: #6dc1cb;
					margin-top: 10px;
					overflow: hidden;
					// white-space: nowrap;
					// text-overflow: ellipsis;
					span {
						display: inline-block;
						float: left;
						max-width: 240px;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
					}
					span.name {
						min-width: 90px;
						color: #6dc1cb;
						margin-right: 15px;
					}
					span.specification {
						display: inline-block;
						max-width: 110px;
						color: #6dc1cb;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
					}
					span.factory {
						display: inline-block;
						max-width: 180px;
						color: #6dc1cb;
						float: right;
						overflow: hidden;
						white-space: nowrap;
						text-overflow: ellipsis;
					}
				}
			}
			.farming-imgs {
				width: 100%;
				img {
					display: inline-block;
					width: 60px;
					height: 60px;

					border-radius: 8px;
					overflow: hidden;
					margin: 10px 10px 0 0;
					cursor: pointer;
				}
			}
		}
	}
}

.nodata {
	height: 460px;
	@include flex(center, center);
	flex-flow: column;

	p {
		color: #6dc1cb;
		@include fontS(20px);
		line-height: 42px;
		margin-bottom: 30px;
	}
}

.loading /deep/ .el-loading-mask {
	background-color: rgba(0, 0, 0, 0);
	padding-top: 200px;
}
</style>



